<template>
    <div class='sing-blog'>
        <h1>{{blog.title}}</h1>
        <article>
            {{blog.content}}
        </article>
        <p>‰ΩúËÄÖ:{{blog.author}}</p>
        <p>ÂàÜÁ±ª:</p>
        <ul>
            <li v-for="item in blog.categories">{{item}}</li>
        </ul>
    </div>
</template>

<script>
export default {
    name:'sing-blog',
    data(){
        return{
            id:this.$route.params.id, // Ëé∑ÂèñÂú∞ÂùÄÊ†èidÁöÑÂèÇÊï∞
            blog:{}
        }
    },
    created:function(){
        this.$http.get('https://vue-test-db-43a08.firebaseio.com/posts/'+this.id+'.json/')
        .then(function(res){
            return res.json()
        }).then(function(data){
            // console.log(data)
            this.blog = data;
        })
    }
}
</script>
<style scoped>
.sing-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eeeeee;
    border: 1px dotted #aaaaaa
}

</style>



