<template>
  <div v-theme:column="'narrow'" class="show-blog">
   <h1>ÂçöÂÆ¢ÊÄªËßà</h1>
   <input type="text" placeholder="ÊêúÁ¥¢üîç" v-model="search" class="serch-blog">
   <div class="single-blog" v-for="blog in filterBlogs">
     <router-link v-bind:to="'/MyBlog/blog/'+blog.id">
       <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
     </router-link>
     <article>
       {{blog.content | sinppet}}
     </article>
   </div>
  </div>
</template>

<script>

export default {
  name: 'show-blogs',
  data(){
    return{
      blogs:[],
      search:''
    }
  },
  created(){
    this.$http.get("https://vue-test-db-43a08.firebaseio.com/posts.json").then(function(res){
      // console.log(res.json());
      //this.blogs = res.body.slice(0,10);
      // console.log(this.blogs);
      return res.json()
    }).then(function(data){
      // console.log(data);
      var blogArrs = [];
      for(let key in data){
        data[key].id = key;
        blogArrs.push(data[key]);
      }
      // console.log(blogArrs);
      this.blogs = blogArrs;
    })
  },
  // ËÆ°ÁÆóÂ±ûÊÄß
  computed:{
    filterBlogs:function(){
      return this.blogs.filter((blog)=>{
        return blog.title.match(this.search);
      })
    }
  }
  
}
</script>

<style scoped>
.show-blog{
  max-width: 800px;
  margin: 20px auto;
}
.single-blog{
  background: #eee;
  box-sizing: border-box;
  padding: 20px;
  margin: 20px 0;

}
.serch-blog{
  outline: none;
  width: 100%;
  height: 30px;
  box-sizing: border-box;
  border-radius: 5px;
}
.single-blog a{
  text-decoration: none;
}
</style>



